.bar_numbers span { font-weight: bold; display: block; font-size: 21px; line-height: 22px; color: #000; padding-top: 10px; padding-bottom: 6px; }

.section_news p { font-size: 17px; line-height: 20px; color: #464646; }

.video_wrapper { position: relative; }

.video_wrapper .video_holder { position: absolute; margin-top: -15px; top: 50%; left: 50%; max-width: 560px; width: 100%; transform: translate(-50%, -50%); }

@media (max-width: 1199.98px) { .video_wrapper .video_holder { max-width: 480px; } }

.list_news .prev_post { display: block; width: 100%; }

.list_news .prev_post img { display: block; }

.list_news li { padding-bottom: 30px; padding-right: 15px; }

.list_news li:last-child { padding-bottom: 0; }

.list_news img { border-radius: 10px; }

.list_news h3 { margin: 13px 0 0 0; margin: 0; padding-top: 13px; font-size: 18px; line-height: 20px; color: #000; font-weight: bold; display: block; }

.list_news h3 a { color: #000; }

.section_news { overflow: hidden; }

.bg_gray { background: #ebeaed; }

.section_labels h2 { font-size: 29px; line-height: 32px; }

.section_labels img { display: block; max-width: 100%; height: auto; }

.main { padding-top: 65px; }

.link_back_page { color: rgba(103, 101, 101, 0.6); font-size: 13px; line-height: 15px; }

.link_back_page i { color: #626161; }

.link_back_page i.icon_arrow_left { font-size: 7px; }

.link_back_page a { color: rgba(103, 101, 101, 0.6); transition: all 0.3s ease; }

.link_back_page a:hover { color: #bc0046; text-decoration: none; }

.link_back_page a:hover i { color: #bc0046; }

.faq_list { margin: 0; padding: 0; list-style: none; }

.faq_list li { border-bottom: 1px solid #959494; padding: 0; }

.faq_list li:last-child { border: 0; }

.faq_list li a { padding-right: 40px; display: block; position: relative; padding-top: 16px; padding-bottom: 11px; }

.faq_list li a:hover { text-decoration: none; }

.faq_list li a span { width: 29px; height: 29px; display: block; position: absolute; top: 10px; right: 10px; }

@media (max-width: 991.98px) { .faq_list li a span { top: 13px; } }

.faq_list li a span:before, .faq_list li a span:after { content: ''; width: 15px; height: 3px; background: #d10146; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); transition: all 0.2s ease; }

.faq_list li a span:after { transform: translate(-50%, -50%) rotate(-90deg); }

.faq_list li.active a { font-weight: bold; }

.faq_list li.active a span:after { transition: all 0.2s ease; transform: translate(-50%, -50%) rotate(0deg); }

.faq_list li p { margin: 0; font-size: 15px; line-height: 22px; color: #464646; font-weight: normal; padding-bottom: 23px; }

.faq_list h3 { margin: 0; font-size: 15px; line-height: 17px; color: #464646; font-weight: normal; }

.faq_list h3 a { color: #464646; }

.post_content h1 { font-size: 38px; line-height: 39px; color: #1a1a18; font-weight: bold; }

.post_content h2 { font-size: 30px; line-height: 32px; font-weight: bold; color: #686867; margin: 0; padding-bottom: 25px; }

.post_content h3 { color: #686867; font-size: 24px; line-height: 26px; margin: 0; padding-bottom: 20px; }

.post_content p { font-size: 16px; line-height: 19px; color: #464646; margin: 0; padding-bottom: 17px; }

.post_content .list_styled { padding-bottom: 16px; }

p.lead { font-size: 17px; line-height: 20px; }

.career_block h3 { font-weight: bold; font-size: 23px; line-height: 24px; color: #1a1a18; margin: 0; }

.career_block p { font-size: 15px; line-height: 22px; margin: 0; }

.career_block img { margin: 0 auto; display: block; }

.career_block .bb-1 { border-bottom: 1px solid #e0e0e0; }

.career_block .bonus { background: linear-gradient(to right, #ece9fc 0%, #e5f2fe 86%, #e7f3fe 100%); text-align: center; font-size: 18px; line-height: 20px; padding: 7px 5px; color: #a61f33; display: block; border-radius: 8px; margin-top: 12px; font-weight: bold; }

.career_block .cntr { width: 80%; margin: 0 auto; }

.career_block .bordered { position: relative; padding-top: 15px; padding-bottom: 15px; min-height: 150px; }

.career_block .bordered:after, .career_block .bordered:before { content: ''; width: 1px; background: #e3e3e3; height: 120px; position: absolute; left: 0; top: 50%; transform: translateY(-50%); }

.career_block .bordered:before { left: auto; right: 0; }

.logo_part { margin-bottom: -8px; }

.start_career { border-bottom: 1px solid #e0e0e0; }

.row_career { position: relative; }

.row_career:after { content: ''; height: 1px; background: #e0e0e0; width: calc( 100% - 16.66667%); position: absolute; right: 0; bottom: 0; }

.row_cases .col-4:nth-child(3n+3) .case_box.opened .drop_case { left: auto; right: 85%; }

.case_box { width: 85%; }

.case_box.opened .drop_case { left: 50%; display: block; }

.case_box .case_prev { flex: 0 0 160px; max-width: 160px; cursor: pointer; }

.case_box .case_cost { flex: 1 1 auto; width: 1%; padding-left: 20px; }

.case_box .case_cost h3 { font-size: 21px; line-height: 22px; color: #313131; font-weight: bold; margin: 0; padding: 0; }

.case_box .case_cost h4 { margin: 0; padding: 0; font-size: 25px; line-height: 26px; font-weight: bold; color: #d10146; }

.drop_case { position: absolute; z-index: 10; top: 0; left: 0; width: 340px; display: none; background: #f2f2f2; border: 2px solid #a70451; border-radius: 9px; padding: 0 10px; }

.drop_case li { border-top: 1px solid #d5d5d5; padding: 11px 7px 12px; }

.drop_case li:first-child { border-top: 0; }

.drop_case span { font-size: 13px; line-height: 17px; color: #313131; flex: 0 0 175px; max-width: 175px; }

.drop_case strong { font-size: 13px; line-height: 17px; color: #a80451; font-weight: bold; flex: 1 1 auto; width: 1%; padding-left: 10px; }

.main .section_about_us { overflow: visible; padding-left: 0; padding-right: 0; }

.main_content_holder { overflow: hidden; }

.text_red { color: #cd0146 !important; }

.bg_white { background: #fff !important; }

.row_course img { display: block; height: auto; border: 2px solid #cfcfcf; border-radius: 11px; }

.hlp_column { flex: 0 0 100%; max-width: 100%; }

.modal_dialog_custom .modal-xl { max-width: 1140px !important; width: 100% !important; padding-left: 10px; padding-right: 10px; }

@media (max-width: 575.98px) { .modal_dialog_custom .modal-xl { background: #000; margin-left: 0; margin-right: 0; } }

.modal_dialog_custom .modal-header { width: 100%; overflow: hidden; border-bottom: 0; }

.modal_dialog_custom .close { opacity: 1; color: rgba(0, 0, 0, 0.62) !important; position: relative; padding: 0; margin: 0; margin-left: auto; }

.modal_dialog_custom .close i { font-style: normal; position: absolute; top: 2px; left: 0; }

.modal_dialog_custom .close span span { font-size: 15px; padding-left: 21px; }
